
import React from 'react';
import './PokemonList.css';
class PokemonList extends React.Component{

  constructor(props){
    super (props);
    this.state = {
      pokemonsList : [],
      pokemonImg: '',
      pokemonItem: {},
      pokemonName: '',
      firstPokemon: 1,
      lasPokemon: 13,
    }
   }


pokemonList = () => {
  const {firstPokemon, lasPokemon} = this.state;
  let pokemonsList = [];
  for(let i = firstPokemon; i < lasPokemon; i++){
  let transitObj = {}
  fetch(`https://pokeapi.co/api/v2/pokemon/${i}`)
  .then(data => data.json())
  .then (data => {
    transitObj.name = data.name
    transitObj.img = data.sprites.front_default
    transitObj.types = data.types.map(item => item.type.name)
    transitObj.id = data.id
    pokemonsList.push(transitObj)

    this.setState({pokemonsList})
    this.setState({firstPokemon: firstPokemon + 12})
    this.setState({lasPokemon: lasPokemon + 12})
  })
  }
}


showPokemon = (id) =>{
document.querySelector('.pokemon-block-2').classList.add('show')
let imgUrl = `https://pokeres.bastionbot.org/images/pokemon/${id}.png`
this.setState({pokemonImg: imgUrl})

let pokemonItem = {}
fetch(`https://pokeapi.co/api/v2/pokemon/${id}`)
.then(data => data.json())
.then(data => {
  pokemonItem.type = data.types.map(item => item.type.name)
  pokemonItem.HP = data.stats[0].base_stat
  pokemonItem.attack = data.stats[1].base_stat
  pokemonItem.defense = data.stats[2].base_stat
  pokemonItem.SP_Attack = data.stats[3].base_stat
  pokemonItem.SP_Defense = data.stats[4].base_stat
  pokemonItem.speed = data.stats[5].base_stat
  pokemonItem.weight = data.weight
  pokemonItem.moves = data.moves.length
  this.setState({pokemonItem})
  this.setState({pokemonName: data.name})
})
}


color = () => {
  let mas = document.querySelectorAll('.span-1');
  for(let i = 0; i < mas.length; i++ ){
    let atr = mas[i].getAttribute('value')
   switch(atr){
     case 'grass':
      mas[i].classList.add('grass')
      break
      case 'fire':
      mas[i].classList.add('fire')
      break
      case 'poison':
      mas[i].classList.add('poison')
      break
      case 'water':
      mas[i].classList.add('water')
      break
      case 'flying':
      mas[i].classList.add('flying')
      break
      case 'bug':
      mas[i].classList.add('bug')
      break
      case 'normal':
      mas[i].classList.add('normal')
      break
      case 'ground':
      mas[i].classList.add('ground')
      break
      case 'electric':
      mas[i].classList.add('electric')
      break
   }
  }
}

componentDidMount(){
  this.pokemonList()
}
  
  render(){
    this.color()
    const {pokemonsList, pokemonImg, pokemonItem, pokemonName} = this.state;
    return(
      <>
      <div className = 'title'>Pokedax</div>
      <div className="pokemon-block">
            <div className = 'pokemon-block-1'>
            {pokemonsList.map(item =>
                    <div id = {item.id} onClick = {this.showPokemon.bind(this, item.id)} className="pokemon-block-item" key = {item.id}>
                      <div className="pokemon-block-item-foto"><img src={item.img} alt="" /></div>
                      <div style={{textTransform: 'capitalize'}} className="pokemon-block-item-name">{item.name}</div>
                      <div className = 'pokemon-block-item-type'>
                      {item.types.map(item =>
                       <div key = {item} style={{textTransform: 'capitalize'}} className="pokemon-block-item-types"> <span value = {item} className = 'span-1'>{item}</span> </div>
                      )}
                      </div>
                    </div>
            )} 
               <button onClick = {this.pokemonList} className = 'btn'>Load more</button>
             </div>   


        <div className = 'pokemon-block-2'>
            <div className = 'pokemon-block-2-item'>
              <div className ='pokemon-block-2-foto'>
                <img src={pokemonImg} alt="" />
              </div>
              <div style={{textTransform: 'capitalize'}} className ='pokemon-block-2-name'>{pokemonName}</div>
                {Object.keys(pokemonItem).map((item) =>
            <div style={{textTransform: 'capitalize'}} className="pokemon-block-2-info" key = {item}>
            <div className="pokemon-block-2-info-name"><p>{item}</p></div>
            <div className="pokemon-block-2-info-skill"><p>{pokemonItem[item]}</p></div>
           </div>
            )}
            </div>
        </div>
      </div>
      </>
    )
  }
}

export default PokemonList;
